import React from 'react'
import News from './News'
import { getNewsGobAr } from './services/news'
import Detalle from './pages/Detalle'


class NewsContainer extends React.Component{
    constructor(props){
        super(props)

        this.state ={
            news: [''],
        }
    }  
    async componentDidMount(){
        const responseJson = await getNewsGobAr()
        this.setState({news: responseJson.articles})
    }

    render(){
        // const name = this.state.news[0].author
        const url = new URL(document.location)
        const hasId = url.searchParams.has('id')
    
        if(hasId){
          return <Detalle id={url.searchParams.get('id')}/>
        }

        return(           

        // <App name = {name}/>
        this.state.news.map((news, index) => <News 
        urlToImage= {news.urlToImage} 
        name = {news.author}
        content = {news.content} 
        publishedAt = {news.publishedAt}
        title = {news.title}
        description = {news.description}
        content = {news.content}
        id = {index}
        key={index}/>       
        
        )
       )       
        
    }
    
}

export default NewsContainer