import React from 'react'
import API_KEY_WEATHER from '../config_keys_weather'
import '../css/clima.css'

class Clima extends React.Component{
    constructor(props){
        super(props)

        this.state ={
            clime: ['0'],
            temp: 0,
            weather: ['0'],
            date: new Date(),
        }
    }  
    componentDidMount(){
        const api_weather = API_KEY_WEATHER
        fetch(`https://api.openweathermap.org/data/2.5/weather?q=Buenos Aires, AR&appid=${api_weather}`)
        .then(res => res.json())
        .then(ClimeJson => this.setState({clime: ClimeJson, temp: ClimeJson.main, weather: ClimeJson.weather}))
    }


    convertToSilsious = (temp) => {
        return Math.floor(temp - 273.15)
    }

 

    render(){    

        // const mainTranslate = () => {
            const mainTrans = {
                Thunderstorm: 'Tormenta',
                Drizzle: 'Llovizna',
                Rain: 'Lluvia',
                Snow: 'Nieve',
                Mist: 'Bruma',
                Smoke: 'Humo',
                Haze: 'Neblina',
                Dust: 'Polvo',
                Fog: 'Niebla',
                Sand: 'Arena',
                Ash: 'Ceniza',
                Squall: 'Chubasco',
                Tornado: 'Tornado',
                Clear: 'Despejado',
                Clouds: 'Nubes'
              }

            // }
        const name = this.state.clime.name
        const icon = this.state.weather[0].icon
        const main = mainTrans[this.state.weather[0].main]
        const temp = this.convertToSilsious(this.state.temp.temp)
        const temp_max = this.convertToSilsious(this.state.temp.temp_max)
        const temp_min = this.convertToSilsious(this.state.temp.temp_min) 
        const hum = this.state.temp.humidity 

        const opt = {weekday: "long", year: "numeric", month: "long", day: "numeric"};
        const date = this.state.date.toLocaleDateString("es-ES", opt)

        return(
        <> 
            <div className="clima">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div><span className="title-clima">El Clima en {name}</span></div>
                            <div className="text-uppercase">{date}</div>
                        </div>
                    </div>    
                    <div className="row">
                        <div className="col text-center">
                            <img className="img-clima" src={`http://openweathermap.org/img/wn/${icon}@2x.png`} alt="{name}"/>
                            <div className="txt-clima-main">{main}</div>
                            <div>Temperatura: {temp}º</div> 
                            <div>Máxima: {temp_max}º - Mínima: {temp_min}º</div>
                            <div>Humedad: {hum}%</div>
                            <div></div>
                        </div>
                    </div>
                </div>         
            </div>  
        </>  
        )      
    }
}

export default Clima