import React from 'react'
import { getNewsGobAr } from '../services/news'
import '../css/detalle.css';

class Detalle extends React.Component{
    constructor(props) {
        super(props)
    
        this.state = {
            newdetail: [''],
        }        
    }
    
        async componentDidMount(){
            const {id} = this.props
            console.log({id}.id)
            const responseJson = await getNewsGobAr()
            this.setState({newdetail: responseJson.articles[{id}.id]}) 
        }

    render(){        
        const {urlToImage, content, publishedAt, title, description, url} = this.state.newdetail

        return(
            <>  <div className="detalle">           
                     <div className="card mb-3">
                     <h2 className="card-title">{title}</h2>
                        <img className="card-img-top" src={urlToImage}  alt={title}/>
                        <div className="card-body">
                            <p className="card-text"><small className="text-muted">{publishedAt}</small></p>                            
                            <h5 className="card-text">{description}</h5>
                            <p className="card-text">{content}</p>
                            <span><a href={url} target="_blank">Leer más</a></span>                            
                        </div>
                    </div>
                </div>    
            </>
        )
    }    
}

export default Detalle

